.component-button{
    display: inline-flex;
    width: 25%;
    flex: 1 0 auto;
}
.component-button.width{
    width: 50%;
}
.component-button button{
    background-color: cadetblue;
    border: 0;
    font-size: 1.5rem;
    margin: 0 1px 0 0;
    flex: 1 0 auto;
    padding: 0;
}
.component-button:last-child button{
    margin-right: 0;
}
.component-button, orange button{
    background-color: coral;
    color: white;
}